.page {
  background-color: $c-light-blue;

  &__content--with-menu {
    overflow: hidden;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transform: translateX(-100%);
    overflow: hidden;

    transition: transform $transition-duration;

    &:target {
      transform: translateX(0);
    }
  }
}
